<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="供应商名称" :inline="true">
                    <el-input v-model.trim="filters.supplier_name" placeholder="请输入供应商名称"></el-input>
                </el-form-item>
                <el-form-item label="联系人" :inline="true">
                    <el-input v-model.trim="filters.contact" placeholder="请输入联系人"></el-input>
                </el-form-item>
                <el-form-item label="移动电话" :inline="true">
                    <el-input v-model.trim="filters.mobile" placeholder="请输入移动电话"></el-input>
                </el-form-item>
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/newCarSupplier/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="supplier_name" label="供应商名称" width="120">
                </el-table-column>
                <el-table-column prop="dict_name" label="供应商类型" width="120">
                </el-table-column>
                <el-table-column prop="contact" label="联系人" width="120">
                </el-table-column>
                <el-table-column prop="mobile" label="移动电话">
                </el-table-column>
                <el-table-column prop="address" label="地址">
                </el-table-column>
                <el-table-column prop="create_time" label="创建时间">
                </el-table-column>
                <el-table-column prop="create_user" label="创建人">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/newCarSupplier/view', query: {id:row.id}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/newCarSupplier/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>

                        <el-button type="text" icon="el-icon-delete" @click="handleStatus(row, 3)" v-if="row.status == 1">禁用</el-button>
                        <el-button type="text" icon="el-icon-delete" @click="handleStatus(row, 1)" v-else>启用</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                total: 1,
                limit: 10,
                loading: false,
                filters: {
                    supplier_name:'',
                    contact:'',
                    mobile:''
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                idx: -1
            }
        },
        created() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.apiNewCarList(params).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                    this.total = res.total;
                })
            },
            handleSearch() {
                this.getData();
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleStatus(row, num){
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: num
                }
                API.apiNewCarEdit(params).then(res => {
                    this.getData();
                })
            }
        }
    }

</script>

<style scoped>

</style>
